}

#s-bl .c-bl:hover{
	background: #f0f4f7;
}

#s-bl .c-bl:last-child{
	margin-bottom: .5em;
}

#s-bl .c-bl:first-child{
	transition: none;
	padding: 0;
	margin-top: 0;
	border:none;
	margin-bottom: 2em;
}

#s-bl .c-bl:not(.b-ex):first-child:hover{
