	background: unset;
}

#s-bl .c-bl.b-ex{
	padding: 0;
	border: none;
	background-color: rgba(0, 0, 0, 0.05);
	transition: none;
}

#s-bl .c-bl.b-ex + .c-bl{
	margin-top: 2em;
}

#s-bl .c-bl.b-ex + .c-bl.b-ex{
	margin-top: 0;
}

#s-bl .c-bl.b-ex:first-child{
	margin-bottom: 1em;
}

#s-bl .c-bl.b-ex:first-child{
	margin-bottom: .5em;
}

#s-bl .b-acc{
	max-height: 0;
	overflow: hidden;
	padding-top: 0;
	margin-bottom: 0;
	display: none;
}

#s-bl .act .b-acc{
	max-height: 100%;
	display: block;
	overflow: hidden;
}

#s-cnt .b-tl {
	margin-bottom: 0.5rem;
	font-size: 18px;
}

#s-cnt .p{
	font-size: 0.9em;
	line-height: 1.5em;
	margin-top: .85em;
	color: #000000;
}

.cc_div .b-tg .c-tgl:disabled{
