}

#c-vln{
	display: table-cell;
	vertical-align: middle;
	position: relative;
}

#cs{
	padding: 0 1.7em;
	width: 100%;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	height: 100%;
}

#s-inr{
	height: 100%;
	position: relative;
	max-width: 45em;
	margin: 0 auto;
	transform: scale(.96);
	opacity: 0;
	padding-top: 4.75em;
	padding-bottom: 4.75em;
