}

.cc_div span.t-lb {
	position: absolute;
	top: 0;
	z-index: -1;
	opacity: 0;
	pointer-events: none;
	overflow: hidden;
}

#c_policy__text{
	height: 31.250em;
	overflow-y: auto;
	margin-top: 1.250em;
}

