@media screen and (min-width: 688px) {
	/** works only on webkit-based browsers **/
	#s-bl::-webkit-scrollbar{
		width: .9em;
		height: 100%;
		background: transparent;
		border-radius: 0 0.250em 0.250em 0;
	}

	#s-bl::-webkit-scrollbar-thumb{
		border: 0.25em solid #fff;
		background: #cfd5db;
		border-radius: 100em;
	}

	#s-bl::-webkit-scrollbar-thumb:hover{
		background: #9199a0;
	}

	#s-bl::-webkit-scrollbar-button {
		width: 10px;
		height: 5px;
	}
}

/** custom checkbox **/
/* The container */
.cc_div .b-tg {
	position: absolute;
	top: 0;
	bottom: 0;
	display: inline-block;
	margin: auto;
	right: 1.2em;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	vertical-align: middle;
}

/* Hide the browser's default checkbox */
.cc_div .b-tg .c-tgl {
	position: absolute;
	cursor: pointer;
	display: block;
	top: 0;
	left: 0;
	margin: 0;
	border: 0;
}

/* Create a custom checkbox */
.cc_div .b-tg .c-tg {
	position: absolute;
	background: #919ea6;
	transition: background-color .25s ease, box-shadow .25s ease;
	pointer-events: none;
}

.cc_div span.t-lb,
.cc_div .b-tg,
.cc_div .b-tg .c-tg,
.cc_div .b-tg .c-tgl{
	width: 3.4em;
	height: 1.5em;
	border-radius: 4em;
}

.cc_div .b-tg .c-tg.c-ro{
	cursor: not-allowed;
}

.cc_div .b-tg .c-tgl ~ .c-tg.c-ro{
	background: #d5dee2;
}

.cc_div .b-tg .c-tgl ~ .c-tg.c-ro:after{
	box-shadow: none;
}

/* Style the checkmark/indicator */
.cc_div .b-tg .c-tg:after {
	content: "";
	position: relative;
	display: block;
	left: 0.125em;
	top: 0.125em;
	width: 1.25em;
	height: 1.25em;
	border: none;
	box-sizing: content-box;
	background: #fff;
	box-shadow: 0 1px 2px rgba(24, 32, 35, .36);
	transition: transform .25s ease;
	border-radius: 100%;
}

/* Show the checkmark when checked */
.cc_div .b-tg .c-tgl:checked ~ .c-tg:after{
	transform: translateX(1.9em);
}

#s-bl table,
#s-bl th,
#s-bl td{
	border: none;
}

#s-bl tbody tr{
	transition: background-color .25s ease;
}

#s-bl tbody tr:hover{
	background: #e9eff4;
}

#s-bl table {
	text-align: left;
	border-collapse: collapse;
	width: 100%;
	padding: 0;
	margin: 0;
	overflow: hidden;
}

#s-bl td,
#s-bl th {
	padding: 0.8em 0.625em;
	text-align: left;
	vertical-align: top;
	font-size: .8em;
	padding-left: 1.2em;
}

#s-bl th {
	font-family: inherit;
	padding: 1.2em 1.2em;
}

#s-bl thead tr:first-child{
	border-bottom: 1px solid #e9edf2;
}

.force--consent #s-cnt,
.force--consent #cs{
	width: 100vw;
}

#cm-ov,
#cs-ov{
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	visibility: hidden;
	opacity: 0;
	background: #070707;
	background: rgba(4, 6, 8, .85);
	display: none;
	transition: none;
}

.show--settings #cs-ov,
.c--anim #cs-ov,
.force--consent .c--anim #cm-ov,
.force--consent.show--consent #cm-ov{
	display: block;
}

#cs-ov{
	z-index: 2;
}

.force--consent .cc_div{
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100vw;
	visibility: hidden;
	transition: visibility .25s linear;
}

.force--consent.show--consent .c--anim .cc_div,
.force--consent.show--settings .c--anim .cc_div{
	visibility: visible;
}

.force--consent #cm{
	position: absolute;
}

.force--consent #cm.bar{
	width: 100vw;
	max-width: 100vw;
}

html.force--consent.show--consent{
	overflow-y: hidden!important;
}

html.force--consent.show--consent,
html.force--consent.show--consent body{
	height: auto!important;
	overflow-x: hidden!important;
}
/** END BLOCK PAGE SCROLL */

/** BEGIN ICONS **/
.cc_div .b-bn .b-tl.exp::before,
.cc_div .act .b-bn .b-tl.exp::before{
	padding: .2em;
	display: inline-block;
	position: absolute;
	content: '';
	margin-right: 15px;
	width: 17px;
	height: 16px;
	background-image: url("../../images/info-icon.svg");
	background-repeat: no-repeat;
	left: 1em;
}

.cc_div .on-i::before{
	border: solid #fff;
	border-width: 0 2px 2px 0;
	padding: .1em;
	display: inline-block;
	padding-bottom: .45em;
	content: '';
	margin: 0 auto;
	transform: rotate(45deg);
	top: .37em;
	left: .75em;
	position: absolute;
}

#s-c-bn::before,
#s-c-bn::after{
	content: '';
	position: absolute;
	left: .82em;
	top: 0;
	height: 1.5em;
	width: 1.5px;
	background: #444d53;
	background: rgba(0, 0, 0, 0.25);
	transform: rotate(45deg);
	border-radius: 1em;
	margin: 0 auto;
}

#s-c-bn::after{
	transform: rotate(-45deg);
}

.cc_div .off-i,
.cc_div .on-i{
	height: 100%;
	width: 50%;
	position: absolute;
	right: 0;
	display: block;
	text-align: center;
	transition: opacity .15s ease;
}

.cc_div .on-i{
	left: 0;
	opacity: 0;
}

.cc_div .off-i::before,
.cc_div .off-i::after{
	right: .8em;
	top: .42em;
	content: ' ';
	height: .7em;
	width: .09375em;
	display: block;
	background: #cdd6dc;
	margin: 0 auto;
	position: absolute;
	transform-origin: center;
}

.cc_div .off-i::before {
	transform: rotate(45deg);
}
.cc_div .off-i::after {
	transform: rotate(-45deg);
}

.cc_div .b-tg .c-tgl:checked ~ .c-tg .on-i{
	opacity: 1;
}
.cc_div .b-tg .c-tgl:checked ~ .c-tg .off-i{
	opacity: 0;
}
/** END ICONS **/

#cm.box.middle,
#cm.cloud.middle{
	top: 50%;
	transform: translateY(-37%);
	bottom: auto;
}

#cm.box.middle.zoom,
#cm.cloud.middle.zoom{
	transform: scale(.95) translateY(-50%);
}

#cm.box.center{
	left: 1em;
	right: 1em;
	margin: 0 auto;
}

/* Start cloud layout */
#cm.cloud {
	max-width: 52.5em;
	margin: 0 auto;
	text-align: center;
	left: 1em;
	right: 1em;
	overflow: hidden;
	padding: 30px;
	width: unset;
}

.cc_div .cloud #c-inr{
	display: table;
	width: 100%;
}

.cc_div .cloud #c-inr-i{
	width: 69%;
	display: table-cell;
	vertical-align: top;
}

.cc_div .cloud #c-txt{
	font-size: 0.85em;
}

.cc_div .cloud #c-bns{
	min-width: 150px;
	display: block;
	vertical-align: middle;
	margin-left: 70px;
}

#cm.cloud .c-bn{
	margin: .625em 0 0 0;
	width: 100%;
}

#cm.cloud .c-bn:first-child{
	margin: 0;
}

#cm.cloud .c-bn:last-child {
	background-color: transparent;
	color: rgba(0, 0, 0, 0.5);
	transition: all .25s ease !important;
}

#cm.cloud .c-bn:last-child:hover {
	background-color: transparent;
	color: rgba(0, 0, 0, 0.75);
}

#cm.cloud.left{
	margin-right: 1.25em;
}

#cm.cloud.right{
	margin-left: 1.25em;
}
/* End cloud layout */

/* Start bar layout */
#cm.bar {
	width: 100%;
	max-width: 100%;
	left: 0;
	right: 0;
	bottom: 0;
	border-radius: 0;
	position: fixed;
	padding: 2em;
}

#cm.bar #c-inr{
	max-width: 32em;
	margin: 0 auto;
}

#cm.bar #c-bns{
	max-width: 33.75em;
}

#cm.bar #cs{
	padding: 0;
}

.cc_div .bar #c-s-in{
	top: 0;
	transform: none;
	height: 100%;
	max-height: 100%;
}

.cc_div .bar #s-hdr,
.cc_div .bar #s-bl,
.cc_div .bar #s-bns {
	padding-left: 1.6em;
	padding-right: 1.6em;
}

.cc_div .bar #cs{
	padding: 0;
}

/* align bar to right by default */
.cc_div .bar #s-inr{
	margin: 0;
	margin-left: auto;
	margin-right: 0;
	border-radius: 0;
	max-width: 32em;
}

.cc_div .bar.left #s-inr{
	margin-left: 0;
	margin-right: auto;
}

/* Force table to not be like tables anymore */
.cc_div .bar #s-bl table,
.cc_div .bar #s-bl thead,
.cc_div .bar #s-bl tbody,
.cc_div .bar #s-bl th,
.cc_div .bar #s-bl td,
.cc_div .bar #s-bl tr,
.cc_div .bar #s-cnt{
	display: block;
}

/* Hide table headers (but not display: none;, for accessibility) */
.cc_div .bar #s-bl thead tr{
	position: absolute;
	top: -9999px;
	left: -9999px;
}

.cc_div .bar #s-bl tr{
	border-top: 1px solid #e3e7ed;
}

.cc_div .bar #s-bl td {
	/* Behave  like a "row" */
	border: none;
	position: relative;
	padding-left: 35%;
}

.cc_div .bar #s-bl td:before {
	position: absolute;
	left: 1em;
	padding-right: 0.625em;
	white-space: nowrap;
	content: attr(data-column);
	color: #000;
	overflow: hidden;
	text-overflow: ellipsis;
}
/* End bar layout */

/* Positions */
#cm.top {
	bottom: auto;
	top: 1.250em;
}

#cm.left{
	right: auto;
	left: 1.250em;
}

#cm.right{
	left: auto;
	right: 1.250em;
}

#cm.bar.left,
#cm.bar.right{
	left: 0;
	right: 0;
}

#cm.bar.top{
	top: 0;
}
/* end positions */

@media screen and (max-width: 688px) {

	#cm,
	#cm.cloud,
	#cm.left,
	#cm.right{
		width: auto;
		max-width: 100%;
		margin: 0;
		padding: 1.4em!important;
		right: 1em;
		left: 1em;
		bottom: 1em;
		display: block;
	}

	.force--consent #cm,
	.force--consent #cm.cloud{
		width: auto;
		max-width: 100vw;
	}

	#cm.top{
		top: 1em;
		bottom: auto;
	}

	#cm.bottom{
		bottom: 1em;
		top: auto;
	}

	#cm.bar.bottom{
		bottom: 0;
	}

	#cm.cloud .c-bn{
		font-size: .85em;
	}

	#s-bns,
	.cc_div .bar #s-bns{
		padding: 1em 1.3em;
	}

	.cc_div .bar #s-inr{
		max-width: 100%;
		width: 100%;
	}

	.cc_div .cloud #c-inr-i{
		padding-right: 0;
	}

	#cs{
		border-radius: 0;
		padding: 0;
	}

	#c-s-in{
		max-height: 100%;
		height: 100%;
		top: 0;
		transform: none;
	}

	.cc_div .b-tg{
		transform: scale(1.1);
		right: 1.1em;
	}

	#s-inr{
		margin: 0;
		padding-bottom: 7.9em;
		border-radius: 0;
	}

	#s-bns{
		height: 7.9em;
	}

	#s-bl,
	.cc_div .bar #s-bl{
		padding: 1.3em;
	}

	#s-hdr,
	.cc_div .bar #s-hdr{
		padding: 0 1.3em;
	}

	/** dynamic table layout **/
	#s-bl table {
		width: 100%;
	}

	#s-inr.bns-t{
		padding-bottom: 10.5em;
	}

	.bns-t #s-bns{
		height: 10.5em;
	}

	.cc_div .bns-t .c-bn{
		font-size: 0.83em;
		padding: .9em 1.6em;
	}

	#s-cnt .b-bn .b-tl{
		padding-top: 1.2em;
		padding-bottom: 1.2em;
	}

	/* Force table to not be like tables anymore */
	#s-bl table,
	#s-bl thead,
	#s-bl tbody,
	#s-bl th,
	#s-bl td,
	#s-bl tr,
	#s-cnt{
		display: block;
	}

	/* Hide table headers (but not display: none;, for accessibility) */
	#s-bl thead tr{
		position: absolute;
		top: -9999px;
		left: -9999px;
	}

	#s-bl tr{
		border-top: 1px solid #e3e7ed;
	}

	#s-bl td {
		/* Behave  like a "row" */
		border: none;
		position: relative;
		padding-left: 35%;
	}

	#s-bl td:before {
		position: absolute;
		left: 1em;
		padding-right: 0.625em;
		white-space: nowrap;
		content: attr(data-column);
		color: #000;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	#cm .c-bn,
	.cc_div .c-bn{
		width: 100%;
		margin-right: 0;
	}

	#s-cnt #s-rall-bn{
		margin-left: 0;
	}

	.cc_div #c-bns{
		flex-direction: column;
	}

	#c-bns button + button,
	#s-cnt button + button{
		margin-top: 0.625em;
		margin-left: 0;
		float: unset;
	}

	#cm.cloud,
	#cm.box{
		left: 1em;
		right: 1em;
		width: auto;
	}

	#cm.cloud.right,
	#cm.cloud.left{
		margin: 0;
	}

	.cc_div .cloud #c-bns,
	.cc_div .cloud #c-inr,
	.cc_div .cloud #c-inr-i{
		display: block;
		width: auto;
		min-width: unset;
	}

	.cc_div .cloud #c-txt{
		font-size: .9em;
	}

	.cc_div .cloud #c-bns{
		margin: 1.625em auto 0;
	}
}

/* Begin IE fixes */
.cc_div.ie #c-vln{
	height: 100%;
	padding-top: 5.62em;
}

.cc_div.ie .bar #c-vln{
	padding-top: 0;
}

.cc_div.ie #cs{
	max-height: 37.5em;
	position: relative;
	top: 0;
	margin-top: -5.625em;
}

.cc_div.ie .bar #cs{
	margin-top:0;
	max-height: 100%;
}

.cc_div.ie #cm{
	border: 1px solid #dee6e9;
}

.cc_div.ie #c-s-in{
	top: 0;
}

.cc_div.ie .b-tg{
	padding-left: 1em;
	margin-bottom: 0.7em;
}

.cc_div.ie .b-tg .c-tgl:checked ~ .c-tg:after{
	left: 1.95em;
}

.cc_div.ie #s-bl table{
	overflow: auto;
}

.cc_div.ie .b-tg .c-tg{
	display: none;
}

.cc_div.ie .b-tg .c-tgl{
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin-bottom: 0.2em;
	height: auto;
}

.cc_div.ie #s-cnt .b-bn .b-tl{
	padding: 1.3em 6.4em 1.3em 1.4em
}

.cc_div.ie  .bar #s-bl td:before{
	display: none;
}

.cc_div.ie .bar #s-bl td{
	padding: 0.8em 0.625em 0.8em 1.2em;
}

.cc_div.ie .bar #s-bl thead tr{
	position: relative;
}

.cc_div.ie .b-tg .t-lb{
	filter: alpha(opacity=0);
}

.cc_div.ie #cm-ov,
.cc_div.ie #cs-ov{
	filter: alpha(opacity=80);
}

/** END IE FIXES **/
#cm.cloud,
.cc_div {
	.button__big {
		background-color: #000;
		color: #fff;
		letter-spacing: 0;
		transition: all 300ms ease-in-out;
		&:hover {
			background-color: lighten(#000, 5%);
		}
	}

	.button--secondary {
		font-size: 12px;
	}
}
